html {
  font-size: 10px;
  font-family: sans-serif;
}
p {
  line-height: 1.5;
  font-size: 1.6rem;
}
h1 {
  font-family: "IBM Plex Mono", monospace;
  text-align: center;
  font-size: 6em;
  font-weight: 100;
  letter-spacing: -2px;
}
@font-face {
  font-family: "IBM Plex Mono", monospace;
  src: url("https://fonts.googleapis.com/css?family=IBM+Plex+Mono");
}
.photo-grid {
  max-width: 1000px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
}
.figure {
  flex-basis: calc(33.33% - 4rem);
  border: 1px solid #d3d3d3;
  padding: 2rem;
  flex-grow: 1;
  margin: 0 2rem 2rem 2rem;
}
.photo {
  width: calc(100% + 4rem);
  margin: -2rem 0rem 0rem -2rem;
}
.button-container {
  display: flex;
  justify-content: center;
}
button {
  flex-basis: 50%;
  background-color: black;
  font-size: 15px;
  color: white;
  border: 4px solid;
  line-height: 2;
  padding: 8px;
  margin: 0 auto;
}
button:hover {
  border-color: white;
  cursor: pointer;
}
.add-button {
  margin: 20px auto;
  display: table;
  padding: 40px;
  background: url("https://image.flaticon.com/icons/svg/60/60740.svg") center
    no-repeat;
  background-size: contain;
  border: none;
}
.form {
  background: white;
  max-width: 360px;
  margin: 0 auto;
  padding: 45px;
  text-align: center;
}
.form input {
  font: san-serif;
  background-color: #f2f2f2;
  width: 100%;
  border: none;
  padding: 15px;
  box-sizing: border-box;
  font-size: 14px;
  margin: 0 0 15px;
}

.form button {
  text-transform: uppercase;
  background: #065784;
  width: 100%;
  padding: 15px;
}
.form button:hover {
  background-color: lightgray;
  text-transform: uppercase;
  cursor: pointer;
  color: black;
}

h2 {
  font-family: "IBM Plex Mono", monospace;
  text-align: center;
  font-size: 3em;
  font-weight: 100;
  letter-spacing: -2px;
}

p {
  font-size: 1.6rem;
  line-height: 1.5;
}

h1 a {
  color: black;
  text-decoration: none;
}

.single-photo {
  max-width: 1000px;
  margin: 0 auto;
  display: flex;
  background: #fff;
  border: black;
}

.single-photo .figure {
  box-shadow: none;
  margin: 0 2rem 0 0;
  border: 0;
  padding: 0;
  flex: 1 0 60%;
  max-width: 60%;
}

.single-photo .comments {
  flex: 1 0 40%;
  max-width: 40%;
}

.single-photo .photo {
  width: 100%;
  margin: 0;
}

.photo {
  width: calc(100% + 4rem);
  margin-left: -2rem;
  margin-top: -2rem;
  max-width: none;
}

.comment {
  width: 100%;
}
.comment p {
  border-bottom: 1px solid #d3d3d3;
  padding: 0.5rem 0;
}
.comment-form input,
.comment-form textarea {
  width: 100%;
  border: 0;
  font-size: 1.3rem;
  padding: 1rem 0;
  border-bottom: 1px solid #d3d3d3;
  outline: none;
  resize: vertical;
}

.button-container {
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
}

.speech-bubble {
  background: url("https://d30y9cdsu7xlg0.cloudfront.net/png/51904-200.png")
    center no-repeat;
  background-size: contain;
  padding: 10px;
  margin: 5px;
  display: inline-block;
  border: none;

  position: relative;
}

.comment-count {
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  color: black;
}

.form {
  width: 360px;
  padding: 0 0 0;
  margin: auto;
}

.form input {
  font-family: sans-serif;
  outline: 0;
  background: #f2f2f2;
  width: 100%;
  border: 0;
  margin: 0 0 15px;
  padding: 15px;
  box-sizing: border-box;
  font-size: 14px;
}

.form button {
  font-family: "Roboto", sans-serif;
  text-transform: uppercase;
  outline: 0;
  background: #065784;
  width: 100%;
  border: 0;
  padding: 15px;
  color: #ffffff;
  font-size: 14px;
  -webkit-transition: all 0.3 ease;
  transition: all 0.3 ease;
  cursor: pointer;
}

.form button:hover,
.form button:active,
.form button:focus {
  background: #7b8589;
}

.loader {
  height: 20%;
  width: 100%;
  display: flex;
  position: fixed;
  align-items: center;
  justify-content: center;
  font-size: 28pt;
}

.comment-button {
  border: 2px solid #f2f2f2;
  background: none;
  flex-basis: 48%;
  display: inline-block;
  line-height: 2;
  text-decoration: none;
  padding: 5px;
  text-align: center;
  font-size: 15px;
  color: black;
  transition: all 0.2s;
  box-sizing: padding-box;
}
